#pag-welcome
{
  display : flex;
  text-align : center;
  align-items : center;
  justify-content: center;
  height: 100vh;

  background-color: black;
  background-image: url('/public/ptproiect.jpg');
  background-size: cover; 
  background-position: center;

  animation : efectpr1 2s ease-in-out;
}

#flexcon
{
      display: flex;
      flex-direction: column; /* Stack items vertically */
      align-items: center; /* Center items horizontally */
}

@keyframes efectpr{

  from
  {
    opacity : 0;
  }
  to
  {
    opacity : 1;
  }

}

@keyframes efectpr1{

    from
    {
      opacity : 0.2;
    }
    to
    {
      opacity : 1;
    }

}



